import { Component } from '@angular/core';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss'],
})
export class ProjectsComponent {
  show: boolean = false;
  loading: boolean = false;

  public showLoader() {
    this.loading = true;
    setTimeout(() => {
      this.loading = false;
      this.show = true;
    }, 2000);
  }
}
